import React from 'react';
import styled from 'styled-components';
import {injectIntl} from "react-intl";

const Message = injectIntl(({success, intl}) => {
    const alertSuccess = intl.formatMessage({id:"alertSuccess"});
    const alertError = intl.formatMessage({id:"alertError"});
    return(
        <MessageWrapped>
            <div className={"alertContainer " + (!success ? "error": "success")}>
                {success ? alertSuccess: alertError}
            </div>
        </MessageWrapped>
    )
});
  
export default Message;

const MessageWrapped = styled.div`
    left: 0px;
    right: 0px;
    top: 100px;
    position: fixed;
    z-index: 15;
    width: 100%;
    max-width: 600px;  
    margin: auto; 

    .alertContainer{
        display: block; 
        text-align: center;
        padding: 20px 0px;
        border-radius: 5px;        
    }
    .error{
        background-color: #f8d7da;
        color: #721c24;
        border: 1px solid #721c24;
    }

    .success{
        background-color: #d4edda;
        color: #155724;
        border: 1px solid #155724;
    }
`;


